#questions{background-color:#f6f9fc;align-self:stretch;padding-top:20px;text-align:center;position:relative;padding:35px}@media(max-width: 425px){#questions{margin-bottom:0}}#questions>:nth-child(1){width:calc(44em + 30vw);display:flex;margin:auto;flex-direction:column;flex-shrink:1}@media(max-width: 1024px){#questions>:nth-child(1){width:calc((.03em + 4.1vw)*23)}}#questions>:nth-child(1) #questions__title{align-self:center;font-size:38px;font-weight:600;margin:30px 0;z-index:1}@media(max-width: 425px){#questions>:nth-child(1) #questions__title{margin:20px 0}}#questions>:nth-child(1)>:nth-child(2){display:flex;flex-direction:column;text-align:start;gap:5px}@media(max-width: 1440px){#questions>:nth-child(1)>:nth-child(2){margin:0}}#questions>:nth-child(1)>:nth-child(2) input{position:absolute;opacity:0;z-index:-1}#questions>:nth-child(1)>:nth-child(2) .questions__item{display:flex;background:rgba(255,255,255,.95);box-shadow:0px 4px 44px rgba(84,104,123,.05);backdrop-filter:blur(12px);border-radius:22px;flex-direction:column;border-radius:12px;padding:25px 35px;overflow:hidden;width:100%;transition:background .3s ease .2s;cursor:pointer}#questions>:nth-child(1)>:nth-child(2) .questions__item:hover{background:rgba(226,226,226,.9);transition:none;box-shadow:0 0 100px rgba(1,194,253,.2)}#questions>:nth-child(1)>:nth-child(2) .questions__item>:nth-child(1){font-size:20px;font-weight:600;width:100%;display:flex;justify-content:space-between;transition:all .2s ease 0s;cursor:pointer}#questions>:nth-child(1)>:nth-child(2) .questions__item>:nth-child(1)::after{content:"❯";transform:rotate(90deg);width:1em;height:1em;text-align:center;transition:all .35s ease 0s}#questions>:nth-child(1)>:nth-child(2) .questions__item>:nth-child(2){display:none;max-height:0;color:#000;line-height:19px;overflow:auto;padding-right:15px;font-weight:300;text-align:start;font-size:16px;font-weight:400}#questions>:nth-child(1)>:nth-child(2) input:checked+.questions__item{background:linear-gradient(268.87deg, #FFC83E -5.09%, #FF7347 51.96%);animation:opacity 1s;box-shadow:inset 0 0 20px #fff}#questions>:nth-child(1)>:nth-child(2) input:checked+.questions__item>:nth-child(1){color:#fff}#questions>:nth-child(1)>:nth-child(2) input:checked+.questions__item>:nth-child(1)::after{transform:rotate(-90deg);padding:1em;margin-right:.2em}#questions>:nth-child(1)>:nth-child(2) input:checked+.questions__item>:nth-child(2){display:block;max-height:100vh}@keyframes opacity{0%{filter:grayscale(1)}100%{filter:grayscale(0)}}